.expense-item{
    display: flex;
    justify-content: space-between;
     align-items: center;
    
    padding: 0.5rem;
    margin: 1rem 0;
    border-top: 2px solid rgb(153, 153, 153);
    border-radius: 15px;
    background-image: linear-gradient(to bottom, #ffffff, #f4f4f4, #e8e8e8, #dddddd, #d2d2d2);
} 

.expense-item__description{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
}
.expense-item h2{
    color:rgb(47, 153, 246);
    font-weight: bold;
    font-size: 1rem;
    flex: 1;
    margin: 0, 1rem;
    padding-left: 0.5rem;
    color: rgb(73, 146, 156);
}
.expense-item__price{
    font-family: 'Bruno Ace SC', cursive;
   background-image: linear-gradient(to right top, #cccccc, #cbbcd3, #d7a7ce, #ea8fba, #fa7598, #f96785, #f75a70, #f34d5a, #ea455b, #e03d5c, #d6355d, #cc2e5d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: 0.5s ease-out;
    

    
}
@media(min-width:500px){
    .expense-item__description{
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1;
        
    }
  
    .expense-item__description h2{

        font-size: 1.5rem;
    }
    .expense-item__price{
        font-size: 1.25rem;
        padding: 0.5rem 1.5rem;
    }
   

}


.changeButton{
    width: 5rem;
    padding: 0.5rem;
    margin: 0.5rem;
    background-color: tomato;
    border-radius: 20px;
    border: 0px solid white;
    color: aliceblue;

}
.changeButton:hover{
    background-color: rgb(250, 42, 6);
    font-weight: bold;
}
.form-input {
    position: relative;
    font-family: "Source Sans Pro", sans-serif;
    font-weight: 600;
    width: 8rem;
    height: 40px;
    border: none;
    padding: 0 10px;
    box-shadow: none;
    outline: none;
    -webkit-transition: all .5s ease;
       -moz-transition: all .5s ease;
        -ms-transition: all .5s ease;
         -o-transition: all .5s ease;
            transition: all .5s ease;
            text-align: center;
            margin-left: 0.75rem;
  }
  .form-input.round {
    border-radius: 40px;
  }
  .form-input.shutter-in {
    border-bottom: 2px solid rgba(0, 0, 0, .2);
    background: transparent;
  }
  .form-input.shutter-in:focus {
    border-color: rgba(0, 0, 0, .3);
    box-shadow: 0 -50px 0 #d5d5d5 inset;
    color: rgb(116, 116, 116);
  }
  @media only screen and (max-width: 400px) {
    .changeButton{
        scale: 0.8;
    }
    .upinp{
        scale: 0.8;
        
        margin-left: 0% !important;
        margin-right: -8% !important;
    }
    .expense-item__price{
    font-size: 120%;

    
    
    }
    h2{
        font-size: 30px;
    }
    .expense-item__description{
        width: 50% !important;
    }
}