.newExpenses{
    margin: 2rem auto;
    padding: 1.25rem;
    border: 0px solid white;
    border-radius: 20px;
    background-image: linear-gradient(to bottom, #ffffff, #f4f4f4, #e8e8e8, #dddddd, #d2d2d2);
    justify-content: center;
    align-items: center;
    width: 50rem;
    max-width: 90%;
    border-top:2px solid rgb(212, 211, 211);
    box-shadow: 10px 24px 30px rgba(160, 157, 157, 0.428);
     
}
@media only screen and (max-width: 400px) {
    .newExpenses{
        margin-top: 0%;
        border-radius: 0px 0 20px 20px;
    }
}