.expense{
    margin: 2rem auto;
    padding: 1.25rem;
    border: 0px solid white;
    border-radius: 20px;
    background-image: linear-gradient(to top, #f2f2f2, #ededed, #e7e7e7, #e2e2e2, #dddddd);
    justify-content: center;
    align-items: center;
    width: 50rem;
    max-width: 90%;
    border-top: 2px solid rgb(179, 178, 178);
    box-shadow: 10px 24px 30px rgba(160, 157, 157, 0.428);
}