@import url('https://fonts.googleapis.com/css2?family=Bruno+Ace+SC&display=swap');
form{
    width: 100%;
    text-align: center;
}
.gifPic{
  width: 30%;
}
.title{
    border: none;
    text-align: center;
    margin: 0px 10px;
    width: 40%;
    height: 1.75rem;
    color: rgb(116, 116, 116);
}
.amount{
    text-align: center;
    margin: 0px 10px;
    width: 21%;
    height: 1.75rem;
    color: rgb(116, 116, 116);
}
.date{
    text-align: center;
    margin: 0px 10px;
    width: 20%;
    height: 1.75rem;
    color:rgb(116, 116, 116) ;
}
.submit{
    cursor: pointer;
    background-image: linear-gradient(to right, #fe7474, #ff6a69, #ff5f5e, #ff5353, #ff4747);
    width: 30%;
    height: 40px;
    font-size: 110%;
    border-radius: 20px;
    color: aliceblue;
    font-weight: 600;
    border: none;
    margin-top: 2rem;
    transition: all 1s ease-in-out;
}
.submit:hover{
  background-image: linear-gradient(to right bottom, #03c9b7, #51cbdb, #92cae6, #bfcbdf, #d2d2d2);
}
.titleHead{
  margin-top: 2%;
  margin-bottom: 5%;
   font-size: 250%;
  
   font-family: 'Bruno Ace SC', cursive;
   background-image: linear-gradient(to right top, #cccccc, #cbbcd3, #d7a7ce, #ea8fba, #fa7598, #f96785, #f75a70, #f34d5a, #ea455b, #e03d5c, #d6355d, #cc2e5d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: 0.5s ease-out;

}
.form-input {
    position: relative;
    font-family: "Source Sans Pro", sans-serif;
    font-weight: 600;
    
    height: 40px;
    border: none;
    padding: 0 10px;
    box-shadow: none;
    outline: none;
    -webkit-transition: all .5s ease;
       -moz-transition: all .5s ease;
        -ms-transition: all .5s ease;
         -o-transition: all .5s ease;
            transition: all .5s ease;
            text-align: center;
            margin-left: 0.75rem;
  }
  .form-input.round {
    border-radius: 40px;
  }
  .form-input.shutter-in {
    border-bottom: 2px solid rgba(0, 0, 0, .2);
    background: transparent;
  }
  .form-input.shutter-in:focus {
    border-color: rgba(0, 0, 0, .3);
    box-shadow: 0 -50px 0 #d5d5d5 inset;
    color: rgb(116, 116, 116);
  }
  .eform{
    text-align: center;
  }

  p{
    cursor: pointer;
    display:inline-flex;
    

    font-family: "Source Sans Pro", sans-serif;
    color: rgb(116, 116, 116);
    text-decoration: underline;
  }

  P:hover{
    
    
    color: greenyellow
  }

  @media only screen and (max-width: 400px) {
    .titleHead{
      font-size: 140%;
    }
    .eform{
      margin-top: 0%;
    }
    .amount{
      width: 80%;
      margin-bottom: 2%;
    }
    .date{
      width: 80%;
    }
    .title{
      width: 80%;
      margin-bottom: 2%;
    }
    .submit{
      margin-top: 2%;
      width: 80%;
      scale: 0.8;
    }
    p{
      margin-top: 0%;
      margin-bottom: -2%;
    }
  }
